import { styled } from "@rp/react/theme"
import { useDeviceInfo } from "@rp/react/web/common/hooks"
import { Box, Paper } from "@rp/react/web/mui/core"
import { RpJsonContentBlockDisplay } from "@rp/react/web/rpjson"

const MentionsModal = styled(Paper)(({ theme }) => ({
  position: "relative",
  width: "100%",
  backgroundColor: theme.palette.elevation.dp01,
  padding: `${theme.spacing(1)} ${theme.spacing(1)} ${theme.spacing(3)}}`,
}))

const MentionsContent = styled(Box)(({ theme }) => ({
  display: "flex",
  flexDirection: "column",
  width: "100%",
  gap: theme.spacing(1),
}))

const MentionsHeader = styled(Box)(({ theme }) => ({
  display: "flex",
  width: "100%",
  justifyContent: "space-between",
  alignItems: "center",

  "& > span": {
    display: "flex",
    alignItems: "center",
    gap: theme.spacing(0.5),

    "& svg": {
      fontSize: 16,
    },
  },

  "& button": {
    padding: 0,
  },
}))

type ArrowProps = {
  translateY: number
}

const MentionsPopperArrow = styled(Box, {
  shouldForwardProp: prop => prop !== "translateY",
})<ArrowProps>(({ theme, translateY }) => ({
  position: "absolute",
  width: 20,
  height: 20,
  fontSize: 10,
  right: 0,
  bottom: 0,
  marginRight: theme.spacing(-1.75),
  transform: `translateY(${translateY}px)`,
  transition: "transform 0.05s",
  "&::before": {
    content: '""',
    margin: "auto",
    display: "block",
    width: 0,
    height: 0,
    borderStyle: "solid",
    borderWidth: "1em 0 1em 1em",
    borderColor: `transparent transparent transparent ${theme.palette.grey[700]}`,
  },
}))

const MentionsCardContainer = styled(Box)(({ theme }) => {
  const { isMobile } = useDeviceInfo()
  return {
    padding: theme.spacing(isMobile ? 1 : 1.5),
    marginLeft: theme.spacing(isMobile ? 0 : 1.5),
    marginRight: theme.spacing(isMobile ? 0 : 1.5),
    backgroundColor: theme.palette.grey[800],
    borderRadius: "8px",
    color: theme.palette.grey[100],
    cursor: "pointer",
    fontSize: theme.typography.body1.fontSize,
    "&:hover": {
      backgroundColor: theme.palette.grey[700],
    },

    // Story Info
    "& .mentions-header": {
      display: "flex",
      gap: theme.spacing(0.5),
      color: theme.palette.grey[300],
      alignItems: "center",
      "& span": {
        whiteSpace: "nowrap",
        fontSize: 11,
        letterSpacing: 1,
      },
    },
  }
})

const MentionsRpJsonContentBlockDisplay = styled(RpJsonContentBlockDisplay)(({ theme }) => ({
  overflowY: "hidden",
  display: "-webkit-box",
  overflowWrap: "break-word",
  WebkitBoxOrient: "vertical",
  WebkitLineClamp: 3,
  color: theme.palette.text.secondary,
  "& p": {
    margin: 0,
  },
}))

const MentionsList = styled("div")(({ theme }) => ({
  "& a": {
    marginBottom: theme.spacing(1.5),
  },
}))

export {
  MentionsRpJsonContentBlockDisplay,
  MentionsModal,
  MentionsContent,
  MentionsHeader,
  MentionsPopperArrow,
  MentionsCardContainer,
  MentionsList,
}
